<template>
  <div class="comment">
  	<h3></h3>
  	<div class="textarea">
  		<textarea placeholder="" v-model='msg'></textarea>
  	</div>
  	<mt-button type="primary" size="large" @click="pubComment"></mt-button>
	<div class="cmtList" v-for="(item,index) in comment">
		<div class="cmt_title">
			<span>{{index+1}}</span><span>{{item.user_name}}</span><span>{{item.add_time}}</span>
		</div>
		<div class="cmt_content">
			<p>{{item.content}}</p>
		</div>
	</div>
  	<mt-button type="primary" size="large" plain @click='loadMore'></mt-button>
  </div>
</template>

<script>
  import {Toast} from 'mint-ui'
export default {
  data () {
    return {
     pageIndex:1,
     comment:[],
     msg:''
    }
  },
  methods:{
  	getComment(){
  		this.$http.get('http://www.liulongbin.top:3005/api/getcomments/'+this.id+'?pageindex='+this.pageIndex).then(res=>{
  			this.comment = this.comment.concat(res.body.message)
  		})
  	},
  	loadMore(){
  		this.pageIndex++;
  		this.getComment()
  	},
    pubComment(){
      this.$http.post("http://www.liulongbin.top:3005/api/postcomment/"+this.$route.params.id,{
        content: this.msg.trim()
      }).then(res=>{
        if(res.body.status === 0){
          var cmt = {
            user_name:"",
            add_time:'2019-11-17',
            content:this.msg.trim()
          }
          this.comment.unshift(cmt)
          this.msg=''
          Toast({
            message:'',
            duration:1000
          })
        }
      })
    }
  },
  created(){
  	this.getComment()
  },
  props:['id']/*,
  filters:{
  	dateFormat:function(d){
  		return d.substring(0,10)
  	}
  }*/
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.comment{
	padding:6px;
	h3{
		margin-bottom:6px;
		padding-bottom:10px;
		border-bottom:solid 2px #999;
	}
	textarea{
		height:50px;
		width:98%;
	}
	.textarea{
		text-align: center;
	}
	.cmtList{
		.cmt_title{
			font-size: 13px;
			display: flex;
			justify-content: space-between;
			background:#ccc;
			padding:6px 0;
			border-radius:3px;
		}
	}
	p{
		text-align: justify;
	}
}

</style>



