    <!-- MAIN -->
    <!-- ROUTER-VIEW -->
    <div class="view_content">
      <transition mode='out-in'>
        <router-view></router-view>
      </transition>
    </div>

    <!-- FOOTER -->
    <div class="page-tabbar"><!-- 
      <div class="page-wrap">
        <div class="page-title">Tabbar</div>
        <div>
          <mt-cell class="page-part" title="" :value="selected" />
        </div>
       
    tabcontainer
        <mt-tab-container class="page-tabbar-container" v-model="selected">
          <mt-tab-container-item id="">
            <mt-cell v-for="n in 10" :title="' ' + n" />
          </mt-tab-container-item>
          <mt-tab-container-item id="">
            <mt-cell v-for="n in 5" :title="' ' + n" />
          </mt-tab-container-item>
          <mt-tab-container-item id="">
            <mt-cell v-for="n in 7" :title="' ' + n" />
          </mt-tab-container-item>
          <mt-tab-container-item id="">
            <div class="page-part">
         cell
              <mt-cell v-for="n in 12" :title="' ' + n" />
            </div>
            <router-link to="/">
         button
              <mt-button type="danger" size="large"></mt-button>
            </router-link>
          </mt-tab-container-item>
        </mt-tab-container>
      </div> -->
   
      <mt-tabbar v-model="selected" fixed>
        <mt-tab-item id="">
          <img slot="icon"  v-if="!(selected=='')" src="../static/img/shouye.png">
          <img slot="icon"  v-if="selected==''" src="../static/img/shouye_ac.png">
          
        </mt-tab-item>

        <mt-tab-item id="">
          <img slot="icon" v-if="!(selected=='')" src="../static/img/huiyuan.png">
          <img slot="icon" v-if="selected==''" src="../static/img/huiyuan_ac.png">
          
        </mt-tab-item>
        <mt-tab-item id="">
          <img slot="icon" v-if="!(selected=='')" src="../static/img/gouwuche.png">
          <img slot="icon" v-if="selected==''" src="../static/img/gouwuche_ac.png">
          
        </mt-tab-item>
        <mt-tab-item id="">
          <img slot="icon" v-if="!(selected=='')" src="../static/img/search.png">
          <img slot="icon" v-if="selected==''" src="../static/img/search_ac.png">
          
        </mt-tab-item>
      </mt-tabbar>
    </div>
  


  </div>
</template>

<script>
export default {
  data(){
    return{
      selected:'',
      test:'a'
    }
  },
  methods:{

  },
  watch:{
    selected(newVal,oldVal){
      switch(newVal){
        case "":this.$router.push({path:'/'});break;
        case "":this.$router.push({path:'/vip'});break;
        case "":this.$router.push({path:'/shopCar'});break;
        case "":this.$router.push({path:'/search'});break;
      }
    }
  }
}
</script>

<style lang='scss' scoped>
#app{
  padding-top: 40px;
  overflow-x: hidden;
  .view_content{
    padding-bottom:80px !important;
  }
}
.v-enter,{
  opacity: 0;
  transform: translateX(100%);
}
.v-leave-to{
  opacity: 0;
  transform: translateX(-100%);
  position:absolute;
}
.v-enter-active,
.v-leave-active{
  transition: all 0.1s ease;
}
</style>



